.App {
  background-color: #FF9900;
  text-align: center;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.App-header {
  background-color: #3090C7;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100%;
  height: 100%;
}

.App-link {
  color: #61dafb;
}

.Message {
  height: 100vh; /* Magic here */
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 100px;
}

.Logo {
  height: 120vh; /* Magic here */
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 120px;
}
